/* =====================================================================
 *   Custom CSS Optimization for EN Purpose
 * =================================================================== */

 @import "_variables";

/* Body */
body {
  line-height: 1.7;
}

#content {
  line-height: 1.7;
}

/* Headings */
h1,
h2 {
  margin-bottom: 12px;
  line-height: 1.3;
}

h3,
h4,
h5,
h6 {
  margin-bottom: 8px;
  line-height: 1.3;
}


h1 { font-size: 53px; }
h2 { font-size: 45px; }
h3 { font-size: 37px; }
h4 { font-size: 30px; }

@media (max-width: 1200px) {
  h1 { font-size: $font-size-h1 * 0.9 }
  h2 { font-size: $font-size-h2 * 0.9 }
  h3 { font-size: $font-size-h3 * 0.9 }
  h4 { font-size: $font-size-h4 * 0.9 }
  h5 { font-size: $font-size-h5 * 0.9 }
  h6 { font-size: $font-size-h6 * 0.9 }
} // < 1200px width

@media (max-width: 768px) {
  h3,
  h4,
  h5,
  h6 { 
    margin-bottom: 12px;
  }
  
  h1 { font-size: $font-size-h1 * 0.8 }
  h2 { font-size: $font-size-h2 * 0.8 }
  h3 { font-size: $font-size-h3 * 0.8 }
  h4 { font-size: $font-size-h4 * 0.8 }
  h5 { font-size: $font-size-h5 * 0.8 }
  h6 { font-size: $font-size-h6 * 0.8 }
} // < 768px width

@media (max-width: 480px) {
  h3,
  h4,
  h5,
  h6 { 
    margin-bottom: 10px;
  }
  
  h1 { font-size: $font-size-h1 * 0.7 }
  h2 { font-size: $font-size-h2 * 0.7 }
  h3 { font-size: $font-size-h3 * 0.7 }
  h4 { font-size: $font-size-h4 * 0.7 }
  h5 { font-size: $font-size-h5 * 0.7 }
  h6 { font-size: $font-size-h6 * 0.7 }
} // < 480px width


 /* Slide Content */
 .slide-content p {
    margin-bottom: 15px;
}

.slide-content {
  h1 {
    font-size: 60px;

    @media (max-width: 1200px) {
      font-size: 50px;
    } // < 1200px width

    @media (max-width: 768px) {
      font-size: 45px;
    } // < 768px width

    @media (max-width: 480px) {
      font-size: 35px;
    } // < 480px width
  }
}
 

/* Menu Main */
.menu-main > li > a {
  font-weight: 700;
  font-size: 18px;
}






